<template>
  <div>
    <div>
      <el-menu :default-active="$route.path" class="el-menu-demo" mode="horizontal" @select="handleSelect" router>
        <el-menu-item @click="showMain()"></el-menu-item>
        <el-menu-item @click="shopCart()"></el-menu-item>
        <el-menu-item @click="orderManage()"></el-menu-item>
        <el-menu-item @click="personalInfoSetting()"></el-menu-item>
        <el-submenu index="5">
          <template slot="title"></template>
          <el-menu-item @click="storeManage()"></el-menu-item>
          <el-menu-item @click="storeInfoSetting()"></el-menu-item>
          <el-menu-item index="/assistantApply" @click="assistantNoPass()"></el-menu-item>
        </el-submenu>
      </el-menu>
    </div>

    <div style="margin-top: 2px">
      <el-tabs v-model="activeName" @tab-click="handleClick" type="border-card">
        <el-tab-pane label="" name="first">
          <div>
            <el-table
              :data="notPass"
              style="width: 100%"
              height="100%">
              <el-table-column
                prop="name"
                label=""
                show-overflow-tooltip>
                <template slot-scope="scope">{{ scope.row.username }}</template>
              </el-table-column>

              <el-table-column
                prop="phone"
                label=""
                show-overflow-tooltip>
                <template slot-scope="scope">{{ scope.row.userId }}</template>
              </el-table-column>

              <el-table-column
                prop="address"
                label=""
                show-overflow-tooltip>
                <template slot-scope="scope">{{ scope.row.useraddress }}</template>
              </el-table-column>

              <el-table-column
                prop="operation">
                <template slot-scope="scope">
                  <el-button size="small" type="success" plain @click="passApply(scope.row.id)"></el-button>
                  <el-button size="small" style="margin-left: 20%" type="danger" plain @click="refuseApply(scope.row.id)"></el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-tab-pane>

        <el-tab-pane label="" name="second">
          <div>
            <el-table
              :data="pass"
              style="width: 100%"
              height="100%">
              <el-table-column
                prop="name"
                label=""
                show-overflow-tooltip>
                <template slot-scope="scope">{{ scope.row.username }}</template>
              </el-table-column>

              <el-table-column
                prop="phone"
                label=""
                show-overflow-tooltip>
                <template slot-scope="scope">{{ scope.row.userId }}</template>
              </el-table-column>

              <el-table-column
                prop="address"
                label=""
                show-overflow-tooltip>
                <template slot-scope="scope">{{ scope.row.useraddress }}</template>
              </el-table-column>

              <el-table-column
                prop="operation">
                <template slot-scope="scope">
                  <el-button size="small" style="margin-left: 40%" type="danger" plain @click="deleteAssistant(scope.row.id)"></el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
  export default {
    name: "AssistantApply",
    inject: ['reload'],

    data() {
      return {
        activeName: 'first',

        notPass: [
          {
            id: '',
            username: '',
            userId: '',
            useraddress: '',
          }
        ],

        pass: [
          {
            id: '',
            username: '',
            userId: '',
            useraddress: '',
          }
        ],
      }
    },

    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },

      handleClick(tab, event) {
        if(this.activeName === 'first') {
          this.assistantNoPass();
        }else {
          this.$axios  // 
            .post('/store/all_assistant', {
              phone: this.$session.get("key"), // 
            })
            .then(successResponse => {
              if (successResponse.data.code === 200) {
                var data = successResponse.data.data;
                this.pass = data;
              }else {
                alert(successResponse.data.message);
              }
            })
            .catch(failResponse => {
              alert("");
            })
        }
      },

      showMain() {
        this.$axios
          .post('/book/renwensheke', {
            id: this.$session.get("key"),
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              var data = successResponse.data.data;
              this.$router.push({path: '/main', query: {mainList: data}});
            }
          })
          .catch(failResponse => {
            alert("");
          })
      },

      shopCart() {
        this.$axios
          .post('/cart/all', {
            id: this.$session.get("key"),
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              var data = successResponse.data.data;
              this.$router.push({path: '/shopCart', query: {cartList: data}});
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert('');
          })
      },

      orderManage() {
        this.$axios  // 
          .post('/order/userweizhifu', {
            userId: this.$session.get("key"), // 
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              var data = successResponse.data.data;
              this.$router.push({path: '/order', query: {unPayList: data}});
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert("");
          })
      },

      personalInfoSetting() {
        this.$axios
          .post('/entity', {
            id: this.$session.get("key"),
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              var data = successResponse.data.data;
              this.$router.push({path: '/personalSetting', query: {personalInfo: data}});
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert('');
          })
      },

      storeManage() {
        this.$axios
          .post('/store/allbooks', {
            phone: this.$session.get("key"),
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              if(successResponse.data.message === "") {
                alert('');
              }else {
                var data = successResponse.data.data;
                this.$router.push({path: '/store', query: {booksList: data}});
              }
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert('');
          })
      },

      storeInfoSetting() {
        this.$axios
          .post('/store/info', {
            phone: this.$session.get("key"),
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              if((successResponse.data.message === "")) {
                alert('');
              }else {
                var data = successResponse.data.data;
                this.$router.push({path: '/storeInfo', query: {storeInfo: data}});
              }
            }else {
              alert("");
            }
          })
          .catch(failResponse => {
            alert('');
          })
      },

      assistantNoPass() {
        this.$axios  // 
          .post('/store/all_assistant_application', {
            phone: this.$session.get("key"), // 
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              if((successResponse.data.message === "") || (successResponse.data.message === "")) {
                alert('');
              }else {
                var data = successResponse.data.data;
                this.reload()
                this.$router.push({path: '/assistantApply', query: {notPass: data}});
              }
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert("");
          })
      },

      passApply(id) {
        this.$axios
          .post('/store/assistant_agree', {
            id: id,
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              alert("");
              this.assistantNoPass();
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert("");
          })
      },

      refuseApply(id) {
        this.$axios
          .post('/store/assistant_refuse', {
            id: id,
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              alert("");
              this.assistantNoPass();
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert("");
          })
      },

      deleteAssistant(id) {
        alert(id)
        this.$axios
          .post('/store/assistant_delete', {
            id: id,
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              alert("");
              //this.assistantNoPass();
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert("");
          })
      },
    },

    mounted() {
      this.notPass = this.$route.query.notPass;
    }
  }
</script>

<style scoped>

</style>



