<template>
  <div>
    <div>
      <el-menu :default-active="$route.path" class="el-menu-demo" mode="horizontal" @select="handleSelect" router>
        <el-menu-item @click="showMain()"></el-menu-item>
        <el-menu-item @click="shopCart()"></el-menu-item>
        <el-menu-item @click="orderManage()"></el-menu-item>
        <el-menu-item index="/personalSetting" @click="personalInfoSetting()"></el-menu-item>
        <el-submenu index="5">
          <template slot="title"></template>
          <el-menu-item @click="storeManage()"></el-menu-item>
          <el-menu-item @click="storeInfoSetting()"></el-menu-item>
          <el-menu-item @click="assistantNoPass()"></el-menu-item>
        </el-submenu>
      </el-menu>
    </div>


    <div style="margin-top: 2px">
      <el-tabs v-model="activeName" type="border-card">
        <el-tab-pane label="" name="first">
          <div>
            <el-form label-width="80px" :model="personInfo">
              <el-form-item label="">
                <el-input v-model="personInfo.name" style="width: 600px; margin-right: 45%" disabled></el-input>
              </el-form-item>

              <el-form-item label="">
                <el-input v-model="personInfo.role" style="width: 600px; margin-right: 45%" disabled></el-input>
                <el-button type="primary" plain v-if="personInfo.role === ''" @click="assistantDialog = true"></el-button>
                <el-button type="primary" plain v-else disabled></el-button>
              </el-form-item>

              <el-form-item label="">
                <el-input v-model="personInfo.address" style="width: 600px; margin-right: 45%" disabled></el-input>
              </el-form-item>
            </el-form>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>

    <div>
      <el-dialog title="" :visible.sync="assistantDialog">
        <el-form :model="assistantApply">
          <el-form-item label="" >
            <el-input v-model="assistantApply.storeId" autocomplete="off" style="width: 500px"></el-input>
          </el-form-item>
        </el-form>
        <div>
          <el-button style="width: 100px" type="primary" @click="apply()"> </el-button>
          <el-button style="width: 100px; margin-left: 20%" @click="assistantDialog = false"> </el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
  export default {
    name: "PersonalSetting",
    inject: ['reload'],

    data() {
      return {
        activeName: 'first',

        assistantDialog: false,

        assistantApply: {
          storeId: '',
        },

        personInfo: {
          userId:'',
          name:'',
          role:'',
          address:''
        },

        personPassword: {
          oldPassword: '',
          newPassword: '',
        },
      }
    },

    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },

      showMain() {
        this.$axios
          .post('/book/renwensheke', {
            id: this.$session.get("key"),
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              var data = successResponse.data.data;
              this.$router.push({path: '/main', query: {mainList: data}});
            }
          })
          .catch(failResponse => {
            alert("");
          })
      },

      shopCart() {
        this.$axios
          .post('/cart/all', {
            id: this.$session.get("key"),
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              var data = successResponse.data.data;
              this.$router.push({path: '/shopCart', query: {cartList: data}});
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert('');
          })
      },

      orderManage() {
        this.$axios  // 
          .post('/order/userweizhifu', {
            userId: this.$session.get("key"), // 
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              var data = successResponse.data.data;
              this.$router.push({path: '/order', query: {unPayList: data}});
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert("");
          })
      },

      personalInfoSetting() {
        this.$axios
          .post('/entity', {
            id: this.$session.get("key"),
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              var data = successResponse.data.data;
              this.reload();
              this.$router.push({path: '/personalSetting', query: {personalInfo: data}});
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert('');
          })
      },

      storeManage() {
        this.$axios
          .post('/store/allbooks', {
            phone: this.$session.get("key"),
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              if(successResponse.data.message === "") {
                alert('');
              }else {
                var data = successResponse.data.data;
                this.$router.push({path: '/store', query: {booksList: data}});
              }
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert('');
          })
      },

      storeInfoSetting() {
        this.$axios
          .post('/store/info', {
            phone: this.$session.get("key"),
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              if((successResponse.data.message === "")) {
                alert('');
              }else {
                var data = successResponse.data.data;
                this.$router.push({path: '/storeInfo', query: {storeInfo: data}});
              }
            }else {
              alert("");
            }
          })
          .catch(failResponse => {
            alert('');
          })
      },

      assistantNoPass() {
        this.$axios  // 
          .post('/store/all_assistant_application', {
            phone: this.$session.get("key"), // 
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              if((successResponse.data.message === "") || (successResponse.data.message === "")) {
                alert('');
              }else {
                var data = successResponse.data.data;
                this.$router.push({path: '/assistantApply', query: {notPass: data}});
              }
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert("");
          })
      },

      apply() {
        this.$axios
          .post('/user/apply_assistant', {
            userId: this.$session.get("key"), // 
            storeId: this.assistantApply.storeId,
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              alert(successResponse.data.message);
              this.assistantDialog = false;
              this.personalInfoSetting();
            }
            else{
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert('');
          })
      },
    },

    mounted() {
      this.personInfo = this.$route.query.personalInfo;
    }
  }
</script>

<style scoped>

</style>



