<template>
  <div>
    <div>
      <el-menu :default-active="$route.path" class="el-menu-demo" mode="horizontal" @select="handleSelect" router>
        <el-menu-item @click="showMain()"></el-menu-item>
        <el-menu-item @click="shopCart()"></el-menu-item>
        <el-menu-item @click="orderManage()"></el-menu-item>
        <el-menu-item @click="personalInfoSetting()"></el-menu-item>
        <el-submenu index="5">
          <template slot="title"></template>
          <el-menu-item index="/store" @click="storeManage()"></el-menu-item>
          <el-menu-item @click="storeInfoSetting()"></el-menu-item>
          <el-menu-item @click="assistantNoPass()"></el-menu-item>
        </el-submenu>
      </el-menu>
    </div>

    <div style="margin-top: 2px">
      <el-tabs v-model="activeName" type="border-card" @tab-click="handleClick()">
        <el-tab-pane label="" name="first">
          <div>
            <div>
              <el-table
                :data="booksList"
                style="width: 100%"
                height="100%">
                <el-table-column
                  prop="picture"
                  label=""
                  width="120">
                  <template slot-scope="scope">
                    <img :src="scope.row.picPath" class="cover">
                  </template>
                </el-table-column>

                <el-table-column
                  prop="name"
                  width="400">
                  <template slot-scope="scope">{{ scope.row.name }}</template>
                </el-table-column>

                <el-table-column
                  prop="category"
                  label=""
                  width="120">
                  <template slot-scope="scope">{{ scope.row.category }}</template>
                </el-table-column>

                <el-table-column
                  prop="author"
                  label=""
                  width="120">
                  <template slot-scope="scope">{{ scope.row.author }}</template>
                </el-table-column>

                <el-table-column
                  prop="number"
                  label=""
                  show-overflow-tooltip>
                  <template slot-scope="scope">{{ scope.row.price }}</template>
                </el-table-column>

                <el-table-column
                  prop="operation">
                  <template slot-scope="scope">
                    <el-button size="medium" type="primary" plain @click="moreInfo(scope.row.id)"></el-button>
                    <el-button style="margin-left: 10%" size="medium" type="danger" plain @click="deleteBook(scope.row.id,scope.row.storeId)"></el-button>
                  </template>
                </el-table-column>
              </el-table>

              <div>
                <el-dialog title="" :visible.sync="bookDialog" :model="bookInfo">
                  <el-card>
                    <div>
                      <el-form :label-position="labelPosition" label-width="80px">
                        <el-form-item label="">
                          <el-input v-model="bookInfo.name" autocomplete="off" style="width: 450px"></el-input>
                        </el-form-item>

                        <el-form-item label="">
                          <el-input v-model="bookInfo.author" autocomplete="off" style="width: 450px"></el-input>
                        </el-form-item>

                        <el-form-item label="">
                          <el-input v-model="bookInfo.publishingHouse" autocomplete="off" style="width: 450px"></el-input>
                        </el-form-item>

                        <el-form-item label="">
                          <el-date-picker
                            value-format="yyyy-MM-dd"
                            v-model="bookInfo.publishingDate"
                            type="date"
                            style="width: 450px">
                          </el-date-picker>
                        </el-form-item>

                        <el-form-item label="">
                          <el-input v-model="bookInfo.price" autocomplete="off" style="width: 450px"></el-input>
                        </el-form-item>

                        <el-form-item label="">
                          <el-select v-model="bookInfo.category" style="width: 450px">
                            <el-option
                              v-for="item in options"
                              :key="item.value"
                              :label="item.label"
                              :value="item.value">
                            </el-option>
                          </el-select>
                        </el-form-item>

                        <el-form-item label="">
                          <el-input v-model="bookInfo.introduction" autocomplete="off" type="textarea" :rows="6" style="width: 450px"></el-input>
                        </el-form-item>
                      </el-form>

                      <div slot="footer" class="dialog-footer">
                        <el-button type="primary" style="width: 30%" @click="updateBook(bookInfo.id)"> </el-button>
                        <el-button style="width: 30%; margin-left: 20%" @click="bookDialog = false"> </el-button>
                      </div>
                    </div>
                  </el-card>
                </el-dialog>
              </div>
            </div>
          </div>
        </el-tab-pane>

        <el-tab-pane label="" name="second">
          <el-table
            :data="orderList"
            style="width: 100%"
            height="100%">
            <el-table-column
              prop="picture"
              label=""
              width="120">
              <template slot-scope="scope">
                <img :src="scope.row.book.picPath" class="cover">
              </template>
            </el-table-column>

            <el-table-column
              prop="name"
              width="300">
              <template slot-scope="scope">{{ scope.row.book.name }}</template>
            </el-table-column>

            <el-table-column
              prop="price"
              label=""
              width="80">
              <template slot-scope="scope">{{ scope.row.book.price }}</template>
            </el-table-column>

            <el-table-column
              prop="user"
              label=""
              width="200">
              <template slot-scope="scope">{{ scope.row.user.username }}</template>
            </el-table-column>

            <el-table-column
              prop="number"
              label=""
              show-overflow-tooltip>
              <template slot-scope="scope">
                <el-input-number v-model="scope.row.order.number" size="small" :min="1" :disabled="true">{{ scope.row.order.number }}</el-input-number>
              </template>
            </el-table-column>

            <el-table-column
              prop="total"
              label=""
              width="120">
              <template slot-scope="scope">{{ scope.row.order.totalPrice }}</template>
            </el-table-column>

            <el-table-column
              prop="operation">
              <template slot-scope="scope">
                <el-button size="medium" type="primary" plain @click="orderMoreInfo(scope.row.order.id)"></el-button>
                <el-button style="margin-left: 10%" size="medium" type="success" plain v-if="scope.row.order.state === ''" @click="sendBook(scope.row.order.id)" disabled></el-button>
                <el-button style="margin-left: 10%" size="medium" type="success" plain v-if="scope.row.order.state === ''" @click="sendBook(scope.row.order.id)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </div>

    <div>
      <el-dialog title="" :visible.sync="addStoreDialog">
        <el-form :model="storeApply">
          <el-form-item label="" >
            <el-input v-model="storeApply.storeName" autocomplete="off" style="width: 500px"></el-input>
          </el-form-item>

          <el-form-item label="" >
            <el-input v-model="storeApply.introduction" type="textarea" :rows="5" autocomplete="off" style="width: 500px"></el-input>
          </el-form-item>
        </el-form>
        <div>
          <el-button style="width: 100px" type="primary" @click="addStore()"> </el-button>
          <el-button style="width: 100px; margin-left: 20%" @click="addStoreDialog = false"> </el-button>
        </div>
      </el-dialog>
    </div>

    <div>
      <el-dialog title="" :visible.sync="newBookDialog">
        <el-card>
          <div>
            <el-form :label-position="labelPosition" label-width="80px" :model="newBookInfo">
              <el-form-item label="">
                <div>
                  <el-upload
                    action="#"
                    list-type="picture-card"
                    :auto-upload="false">
                    <i slot="default" class="el-icon-plus"></i>
                    <div slot="file" slot-scope="{file}">
                      <img
                        class="el-upload-list__item-thumbnail cover"
                        style="width: 100%;"
                        :src="file.url" alt=""
                      >
                    </div>
                  </el-upload>
                </div>
              </el-form-item>

              <el-form-item label="">
                <el-input v-model="newBookInfo.name" autocomplete="off" style="width: 450px" placeholder=""></el-input>
              </el-form-item>

              <el-form-item label="">
                <el-input v-model="newBookInfo.author" autocomplete="off" style="width: 450px" placeholder=""></el-input>
              </el-form-item>

              <el-form-item label="">
                <el-input v-model="newBookInfo.publishHouse" autocomplete="off" style="width: 450px" placeholder=""></el-input>
              </el-form-item>

              <el-form-item label="">
                <el-date-picker
                  v-model="newBookInfo.publishDate"
                  type="date"
                  style="width: 450px"
                  placeholder="">
                </el-date-picker>
              </el-form-item>

              <el-form-item label="">
                <el-input v-model="newBookInfo.price" autocomplete="off" style="width: 450px" placeholder=""></el-input>
              </el-form-item>

              <el-form-item label="">
                <el-select v-model="newBookInfo.category" style="width: 450px" placeholder="">
                  <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="">
                <el-input v-model="newBookInfo.introduction" autocomplete="off" type="textarea" :rows="6" style="width: 450px" placeholder=""></el-input>
              </el-form-item>
            </el-form>

            <div slot="footer" class="dialog-footer">
              <el-button type="primary" style="width: 30%" @click="newBookDialog = false"> </el-button>
              <el-button style="width: 30%; margin-left: 20%" @click="newBookDialog = false"> </el-button>
            </div>
          </div>
        </el-card>
      </el-dialog>
    </div>

    <div>
      <el-dialog title="" :visible.sync="orderDialog">
        <el-card>
          <div>
            <el-dialog title="" :visible.sync="orderDialog">
              <el-card>
                <div>
                  <el-form :label-position="labelPosition" label-width="80px" :model="orderInfo">
                    <el-form-item label="">
                      <el-input v-model="orderInfo.book.name" autocomplete="off" style="width: 450px" disabled></el-input>
                    </el-form-item>

                    <el-form-item label="">
                      <el-input v-model="orderInfo.book.price" autocomplete="off" style="width: 450px" disabled></el-input>
                    </el-form-item>

                    <el-form-item label="">
                      <el-input v-model="orderInfo.order.number" autocomplete="off" style="width: 450px" disabled></el-input>
                    </el-form-item>

                    <el-form-item label="">
                      <el-input v-model="orderInfo.order.totalPrice" autocomplete="off" style="width: 450px" disabled></el-input>
                    </el-form-item>

                    <el-form-item label="">
                      <el-input v-model="orderInfo.user.username" autocomplete="off" style="width: 450px" disabled></el-input>
                    </el-form-item>

                    <el-form-item label="">
                      <el-input v-model="orderInfo.user.address" autocomplete="off" style="width: 450px" disabled></el-input>
                    </el-form-item>

                    <el-form-item label="">
                      <el-input v-model="orderInfo.user.id" autocomplete="off" style="width: 450px" disabled></el-input>
                    </el-form-item>
                  </el-form>

                  <div slot="footer" class="dialog-footer">
                    <el-button type="primary" style="width: 30%" @click="orderDialog = false"> </el-button>
                    <el-button style="width: 30%; margin-left: 20%" @click="orderDialog = false"> </el-button>
                  </div>
                </div>
              </el-card>
            </el-dialog>
          </div>
        </el-card>
      </el-dialog>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Store",
    inject: ['reload'],

    data() {
      return {
        input: '',
        activeName: 'first',
        num: 1,
        value: '',
        labelPosition: 'left',
        dialogImageUrl: '',

        addStoreDialog: false,
        newBookDialog: false,
        bookDialog: false,
        orderDialog: false,

        booksList: [],

        bookInfo: {
          id: '',
          name: '',
          author: '',
          storeId: '',
          publishingHouse: '',
          publishingDate: '2019-05-05',
          price: '60',
          category: '',
          introduction: '',
        },

        storeApply: {
          storeName: '',
          introduction: '',
        },

        newBookInfo: {
          name: '',
          author: '',
          storeId: '',
          publishHouse: '',
          publishDate: '',
          category: '',
          introduction: '',
        },

        orderList: [
          {
            picture: '',
            name: '',
            price: '',
            username: '',
            number: '',
            totalPrice: '',
            state: ''
          }
        ],

        orderInfo: {
          order: {
            id: '',
            bookId: '',
            userId: '',
            number: '',
            totalPrice: '',
            placeDate: '',
            state: '',
          },

          user: {
            id: '',
            username: '',
            password: '',
            address: '',
            role: '',
          },

          book: {
            id: '',
            storeId: '',
            name: '',
            author: '',
            publishingHouse: '',
            price: '',
            category: '',
            introduction: '',
            picPath: '',
          },

          store: {
            id: '',
            name: '',
            phone: '',
            address: '',
            introduction: ''
          }
        },

        options: [
          {
            value: '',
            label: ''
          },

          {
            value: '',
            label: '',
          },

          {
            value: '',
            label: ''
          },

          {
            value: '',
            label: ''
          },

          {
            value: '',
            label: ''
          },

          {
            value: '',
            label: ''
          },

          {
            value: '',
            label: ''
          },

          {
            value: '',
            label: ''
          }],
      }
    },

    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },

      handleClick(tab, event) {
        if(this.activeName === 'first') {
          this.storeManage();
        }else {
          alert("...");
          this.$axios  // 
            .post('order/all', {
              phone: this.$session.get("key"), // 
            })
            .then(successResponse => {
              if (successResponse.data.code === 200) {
                var data = successResponse.data.data;
                this.orderList = data;
              }else {
                alert(successResponse.data.message);
              }
            })
            .catch(failResponse => {
              alert("");
            })
        }
      },

      handleDelete(index, row) {
        console.log(index, row);
      },

      showMain() {
        this.$axios
          .post('/book/renwensheke', {
            id: this.$session.get("key"),
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              var data = successResponse.data.data;
              this.$router.push({path: '/main', query: {mainList: data}});
            }
          })
          .catch(failResponse => {
            alert("");
          })
      },

      shopCart() {
        this.$axios
          .post('/cart/all', {
            id: this.$session.get("key"),
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              var data = successResponse.data.data;
              this.$router.push({path: '/shopCart', query: {cartList: data}});
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert('');
          })
      },

      orderManage() {
        this.$axios  // 
          .post('/order/userweizhifu', {
            userId: this.$session.get("key"), // 
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              var data = successResponse.data.data;
              this.$router.push({path: '/order', query: {unPayList: data}});
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert("");
          })
      },

      personalInfoSetting() {
        this.$axios
          .post('/entity', {
            id: this.$session.get("key"),
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              var data = successResponse.data.data;
              this.$router.push({path: '/personalSetting', query: {personalInfo: data}});
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert('');
          })
      },

      storeManage() {
        this.$axios  // 
          .post('/store/allbooks', {
            phone: this.$session.get("key"), // 
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              var data = successResponse.data.data;
              this.reload();
              this.$router.push({path: '/store', query: {booksList: data}});
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert("");
          })
      },

      storeInfoSetting() {
        this.$axios
          .post('/store/info', {
            phone: this.$session.get("key"),
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              if((successResponse.data.message === "")) {
                alert('');
              }else {
                var data = successResponse.data.data;
                this.$router.push({path: '/storeInfo', query: {storeInfo: data}});
              }
            }else {
              alert("");
            }
          })
          .catch(failResponse => {
            alert('');
          })
      },

      assistantNoPass() {
        this.$axios  // 
          .post('/store/all_assistant_application', {
            phone: this.$session.get("key"), // 
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              if((successResponse.data.message === "") || (successResponse.data.message === "")) {
                alert('');
              }else {
                var data = successResponse.data.data;
                this.$router.push({path: '/assistantApply', query: {notPass: data}});
              }
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert("");
          })
      },

      moreInfo(id) {
        this.$axios // 
          .post('/book/info', {
            id: id, // 
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              this.bookInfo = successResponse.data.data;
              this.bookDialog = true;
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert("");
          })
      },

      updateBook(id) {
        this.$axios
          .post('book/update', {
            id: id,
            name: this.bookInfo.name,
            author: this.bookInfo.author,
            publishingHouse: this.bookInfo.publishingHouse,
            publishingDate: this.bookInfo.publishingDate,
            price: this.bookInfo.price,
            category: this.bookInfo.category,
            introduction: this.bookInfo.introduction,
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              alert(successResponse.data.message);
              var data = successResponse.data.data;
              this.reload();
              this.$router.push({path: '/store', query: {booksList: data}});
              this.bookDialog = false;
            }
          })
          .catch(failResponse => {
            alert('');
          })
      },

      deleteBook(id) {
        this.$axios
          .post('/book/delete', {
            id: id, // 
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              alert(successResponse.data.message);
              var data = successResponse.data.data;
              this.reload();
              this.$router.push({path: '/store', query: {booksList: data}});
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert("");
          })
      },

      orderMoreInfo(id) {
        this.$axios // 
          .post('order/info', {
            phone: this.$session.get("key"), // 
            id: id, // 
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              this.orderInfo = successResponse.data.data;
              this.orderDialog = true;
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert("");
          })
      },

      sendBook(id) {
        this.$axios
          .post('/order/send', {
            id: id, // 
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              alert(successResponse.data.message); // 
              var data = successResponse.data.data; // 
              this.reload();
              this.orderList = data;
            }
          })
          .catch(failResponse => {
            alert('');
          })
      }
    },

    mounted() {
      this.booksList = this.$route.query.booksList;
    }
  }
</script>

<style scoped>
  .cover {
    width: 100px;
    height: 150px;
    margin-top: 2px;
    overflow: hidden;
    cursor: pointer;
  }
</style>



