<template>
  <div>
    <div>
      <el-menu :default-active="$route.path" class="el-menu-demo" mode="horizontal" @select="handleSelect" router>
        <el-menu-item @click="showMain()"></el-menu-item>
        <el-menu-item @click="shopCart()"></el-menu-item>
        <el-menu-item @click="orderManage()"></el-menu-item>
        <el-menu-item @click="personalInfoSetting()"></el-menu-item>
        <el-submenu index="5">
          <template slot="title"></template>
          <el-menu-item @click="storeManage()"></el-menu-item>
          <el-menu-item index="/storeInfo" @click="storeInfoSetting()"></el-menu-item>
          <el-menu-item @click="assistantNoPass()"></el-menu-item>
        </el-submenu>
      </el-menu>
    </div>

    <div style="margin-top: 2px">
      <el-tabs v-model="activeName" @tab-click="handleClick" type="border-card">
        <el-tab-pane label="" name="first">
          <div>

            <el-form-item label="">
              <el-input v-model="storeInfo.id" style="width: 600px; margin-right: 45%" disabled></el-input>
            </el-form-item>

            <el-form :label-position="labelPosition" label-width="80px" :model="storeInfo">
              <el-form-item label="">
                <el-input v-model="storeInfo.name" style="width: 600px; margin-right: 45%"></el-input>
              </el-form-item>

              <el-form-item label="">
                <el-input v-model="storeInfo.phone" style="width: 600px; margin-right: 45%"></el-input>
              </el-form-item>

              <el-form-item label="">
                <el-input v-model="storeInfo.address" style="width: 600px; margin-right: 45%"></el-input>
              </el-form-item>

              <el-form-item label="">
                <el-input v-model="storeInfo.introduction" type="textarea" :rows="5" style="width: 600px; margin-right: 45%"></el-input>
              </el-form-item>

              <el-form-item>
                <el-button type="success" plain style="width: 200px; margin-right: 10%" @click="infoUpdate()"></el-button>
              </el-form-item>
            </el-form>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
  export default {
    name: "StoreInfo",
    inject: ['reload'],

    data() {
      return {
        input: '',
        activeName: 'first',
        labelPosition: 'right',

        storeInfo: {},

        storePassword: {
          oldPassword: '',
          newPassword: '',
        },
      }
    },

    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },

      handleClick(tab, event) {
        console.log(tab, event);
      },

      showMain() {
        this.$axios
          .post('/book/renwensheke', {
            id: this.$session.get("key"),
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              var data = successResponse.data.data;
              this.$router.push({path: '/main', query: {mainList: data}});
            }
          })
          .catch(failResponse => {
            alert("");
          })
      },

      shopCart() {
        this.$axios
          .post('/cart/all', {
            id: this.$session.get("key"),
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              var data = successResponse.data.data;
              this.$router.push({path: '/shopCart', query: {cartList: data}});
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert('');
          })
      },

      orderManage() {
        this.$axios  // 
          .post('/order/userweizhifu', {
            userId: this.$session.get("key"), // 
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              var data = successResponse.data.data;
              this.$router.push({path: '/order', query: {unPayList: data}});
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert("");
          })
      },

      personalInfoSetting() {
        this.$axios
          .post('/entity', {
            id: this.$session.get("key"),
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              var data = successResponse.data.data;
              this.$router.push({path: '/personalSetting', query: {personalInfo: data}});
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert('');
          })
      },

      storeManage() {
        this.$axios
          .post('/store/allbooks', {
            phone: this.$session.get("key"),
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              if(successResponse.data.message === "") {
                alert('');
              }else {
                var data = successResponse.data.data;
                this.$router.push({path: '/store', query: {booksList: data}});
              }
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert('');
          })
      },

      storeInfoSetting() {
        this.$axios
          .post('/store/info', {
            phone: this.$session.get("key"),
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              if((successResponse.data.message === "")) {
                alert('');
              }else {
                var data = successResponse.data.data;
                this.$router.push({path: '/storeInfo', query: {storeInfo: data}});
              }
            }else {
              alert("");
            }
          })
          .catch(failResponse => {
            alert('');
          })
      },

      assistantNoPass() {
        this.$axios  // 
          .post('/store/all_assistant_application', {
            phone: this.$session.get("key"), // 
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              if((successResponse.data.message === "") || (successResponse.data.message === "")) {
                alert('');
              }else {
                var data = successResponse.data.data;
                this.$router.push({path: '/assistantApply', query: {notPass: data}});
              }
            }else {
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert("");
          })
      },

      infoUpdate() {
        this.$axios
          .post('/store/update', {
            phone: this.storeInfo.phone,
            id: this.storeInfo.id,
            name: this.storeInfo.name,
            address: this.storeInfo.address,
            introduction: this.storeInfo.introduction,
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              alert("");
              var data = successResponse.data.data;
              this.$router.push({path: '/storeInfo', query: {storeInfo: data}});
            }
            else{
              alert(successResponse.data.message);
            }
          })
          .catch(failResponse => {
            alert('');
          })
      },
    },

    mounted() {
      this.storeInfo = this.$route.query.storeInfo;
    }
  }
</script>

<style scoped>

</style>



